import React, { useState } from 'react';
import clsx from 'clsx';
import file from '../../../../../assets/icons/File.svg';
import { useAppSelector } from '../../../../../redux';
import { UserRoleEnum } from '../../../../../interfaces';
import { useAuthContext } from '../../../../context';
import { UploadFileButton } from '../../../../components/UploadFileButton';

const Libraries = (): JSX.Element => {
  const authContext = useAuthContext();
  const workshop = useAppSelector((state) => state.workshopDetail);
  const [librariesType, setLibrariesType] = useState<UserRoleEnum>(authContext.state.user.role);
  return (
    <>
      <div className="library">
        <h1>Document Library</h1>
        <div className="library-btns">
          <button
            className={clsx({ active: librariesType === UserRoleEnum.admin })}
            onClick={() => setLibrariesType(UserRoleEnum.admin)}>
            Admin
          </button>
          <button
            className={clsx({ active: librariesType === UserRoleEnum.instructor })}
            onClick={() => setLibrariesType(UserRoleEnum.instructor)}>
            Instructor
          </button>
          <button
            className={clsx({ active: librariesType === UserRoleEnum.educator })}
            onClick={() => setLibrariesType(UserRoleEnum.educator)}>
            Educators
          </button>
        </div>
        {librariesType === UserRoleEnum.admin && (
          <>
            {workshop.data?.docs
              ?.filter((item) => item.createdByRole === librariesType)
              .map((attachment) => (
                <div className="files">
                  <div className="file">
                    <img src={file} alt="" />
                    <a href={attachment.link}>{attachment.description}</a>
                  </div>
                </div>
              ))}
            {authContext.state.user.role === librariesType && (
              <>
                <UploadFileButton
                  name="file"
                  label="Upload Document"
                  text="select pdf,docs, docs file only"
                  onChange={(e) => {}}
                />
              </>
            )}
          </>
        )}
        {librariesType === UserRoleEnum.instructor && (
          <>
            {workshop.data?.docs
              ?.filter((item) => item.createdByRole === librariesType)
              .map((attachment) => (
                <div className="files">
                  <div className="file">
                    <img src={file} alt="" />
                    <a href={attachment.link}>Artem Sazonov.docx</a>
                  </div>
                </div>
              ))}
            {authContext.state.user.role === librariesType && (
              <>
                <UploadFileButton
                  name="file"
                  label="Upload Document"
                  text="select pdf,docs, docs file only"
                  onChange={(e) => {}}
                />
              </>
            )}
          </>
        )}
        {librariesType === UserRoleEnum.educator && (
          <>
            {authContext.state.user.role === UserRoleEnum.educator && (
              <>
                {workshop.data?.docs
                  ?.filter((item) => item.createdByRole === librariesType)
                  .filter((item) => item.createdBy === authContext.state.user.id)
                  .map((attachment) => (
                    <div className="files">
                      <div className="file">
                        <img src={file} alt="" />
                        <a href={attachment.link}>Artem Sazonov.docx</a>
                      </div>
                    </div>
                  ))}
              </>
            )}

            {authContext.state.user.role !== UserRoleEnum.educator && (
              <>
                {workshop.data?.docs
                  ?.filter((item) => item.createdByRole === librariesType)
                  .map((attachment) => (
                    <div className="files">
                      <div className="file">
                        <img src={file} alt="" />
                        <a href={attachment.link}>Artem Sazonov.docx</a>
                      </div>
                    </div>
                  ))}
              </>
            )}

            {authContext.state.user.role === librariesType && (
              <>
                <UploadFileButton
                  name="file"
                  label="Upload Document"
                  text="select pdf,docs, docs file only"
                  onChange={(e) => {}}
                />
              </>
            )}
          </>
        )}
      </div>
    </>
  );
};
export default Libraries;
