import React, { useEffect } from 'react';
import { WorkshopCard } from '../../components';
import { useAppDispatch, useAppSelector } from '../../../redux';
import { pastWorkshopsListThunk } from '../../../redux/workshops';

export function PastWorkshopsView(): JSX.Element {
  const workshops = useAppSelector((state) => state.workshopsList);
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(pastWorkshopsListThunk());
  }, [dispatch]);

  return (
    <div>
      {!workshops.loading && (
        <>
          <div className="items content">
            {workshops.past.map((item, index) => (
              <div className="" key={index}>
                <WorkshopCard data={item} loading={false} />
              </div>
            ))}
          </div>
        </>
      )}

      {workshops.loading && (
        <div className="items content">
          {Array(10)
            .fill(1)
            .map((item, index) => (
              <div className="" key={index}>
                <WorkshopCard loading={true} />
              </div>
            ))}
        </div>
      )}
    </div>
  );
}
