import React, { ChangeEvent, createRef, useContext } from 'react';
import profileImg from '../../../assets/images/Group 7.png';
import { AuthContext } from '../../context';
import { FormikConfig } from 'formik/dist/types';
import { UpdateEducatorValidator } from '../../../validators';
import { ErrorMessage, Form, Formik, FormikHelpers } from 'formik';
import { API } from '../../../utility/api';
import { toast } from 'react-toastify';
import { FormikValidator } from '../../../utility';

export default function EducatorProfileView(): JSX.Element {
  const authContext = useContext(AuthContext);
  const userFormSubmitRef = createRef<HTMLButtonElement>();
  const formik: FormikConfig<UpdateEducatorValidator> = {
    initialValues: {
      ...authContext.state.user,
      school: {
        ...authContext.state.user!.school,
      },
    },

    validateOnChange: true,
    onSubmit(
      values: UpdateEducatorValidator,
      formikHelpers: FormikHelpers<any>,
    ): void | Promise<any> {
      API.Auth.updateUser(values)
        .then((response) => {
          authContext.actions.updateState(response.data);
          toast('User Updated Successfully', {
            type: 'success',
          });
        })
        .catch((error) => {
          console.error(error.response);
          const errors = FormikValidator.serverErrors(error.response);
          if (errors) {
            formikHelpers.setErrors(errors);
          }
        });
      return undefined;
    },
    validate: FormikValidator.validator(UpdateEducatorValidator),
  };

  const onChangeFile = (
    event: ChangeEvent,
    setFieldValue: (field: string, value: any, shouldValidate?: boolean) => void,
  ) => {
    const target = event.target as HTMLInputElement;
    const files = target.files;
    API.Media.uploadFile(files[0])
      .then((response) => {
        console.log(response);
        setFieldValue(target.name, response.data.path);
      })
      .catch((error) => {
        console.error(error);
      });
  };

  return (
    <div className="instructor-profile-section">
      <div className="user-section instructor-profile-btns">
        <div className="user-work">
          <h1>
            {authContext.state.user.firstName} {authContext.state.user.lastName}
          </h1>
          <button
            type="button"
            className="btn btn-primary"
            style={{
              background: '#0063B2',
              color: '#ffffff',
            }}
            onClick={() => {
              userFormSubmitRef.current.click();
            }}>
            Update Profile
          </button>
        </div>
      </div>
      <div className="content">
        <Formik {...formik}>
          {(formikProps) => (
            <Form>
              <div className="form-design">
                <div className="instructor-info">
                  <div className="profile-pic-section">
                    <div className="profile-img d-flex flex-column w-100 align-items-center justify-content-center">
                      <div className="circular-image">
                        <img
                          alt=""
                          className="profile-image"
                          src={formikProps.values.profile || profileImg}
                        />
                        <label className="profile-edit">
                          <span className="material-icons">add_a_photo</span>
                          <input
                            type="file"
                            name="profile"
                            onChange={(event) => onChangeFile(event, formikProps.setFieldValue)}
                          />
                        </label>
                      </div>
                      <p>Jerry Dawson</p>
                    </div>
                    <div className="input-field">
                      <label htmlFor="address1">Address 1</label>
                      <input
                        type="text"
                        className="input"
                        name="address1"
                        placeholder="Address 1"
                        value={formikProps.values.address1}
                        onChange={formikProps.handleChange}
                      />
                      <ErrorMessage
                        name="address1"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                  </div>
                  <div className="data">
                    <h1>Personal Details</h1>
                    <div className="input-fields">
                      <div className="input-field">
                        <label htmlFor="firstName">First Name</label>
                        <input
                          type="text"
                          className="input"
                          name="firstName"
                          placeholder="First Name"
                          value={formikProps.values.firstName}
                          onChange={formikProps.handleChange}
                        />
                        <ErrorMessage
                          name="firstName"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                      <div className="input-field">
                        <label htmlFor="lastName">Last Name</label>
                        <input
                          type="text"
                          className="input"
                          name="lastName"
                          placeholder="Last Name"
                          value={formikProps.values.lastName}
                          onChange={formikProps.handleChange}
                        />
                        <ErrorMessage
                          name="lastName"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                    </div>
                    <div className="input-fields">
                      <div className="input-field">
                        <label htmlFor="email">Email Address</label>
                        <input
                          type="email"
                          className="input"
                          name="email"
                          placeholder="Enter your Email"
                          value={formikProps.values.email}
                          onChange={formikProps.handleChange}
                        />
                        <ErrorMessage
                          name="email"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                      <div className="input-field">
                        <label htmlFor="phoneNumber">Phone Number</label>
                        <input
                          type="text"
                          className="input"
                          name="phoneNumber"
                          placeholder="Enter your Phone Number"
                          value={formikProps.values.phoneNumber}
                          onChange={formikProps.handleChange}
                        />
                        <ErrorMessage
                          name="phoneNumber"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                    </div>
                    <div className="input-fields">
                      <div className="input-field">
                        <label htmlFor="code">Address 2</label>
                        <input
                          type="text"
                          className="input"
                          name="address2"
                          placeholder="Address 2"
                          value={formikProps.values.address2}
                          onChange={formikProps.handleChange}
                        />
                        <ErrorMessage
                          name="address2"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                      <div className="input-field">
                        <label htmlFor="code">Zip Code</label>
                        <input
                          type="text"
                          className="input"
                          name="zip"
                          placeholder="Zip Code"
                          value={formikProps.values.zip}
                          onChange={formikProps.handleChange}
                        />
                        <ErrorMessage
                          name="zip"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="summary">
                  <label htmlFor="bio">Personal Summary</label>
                  <textarea
                    name="bio"
                    className="input"
                    cols={0}
                    rows={3}
                    value={formikProps.values.bio}
                    onChange={formikProps.handleChange}
                  />
                  <ErrorMessage
                    name="bio"
                    render={(error) => <span className="error">{error}</span>}
                  />
                </div>
              </div>
              <div className="form-design">
                <h1 className="form-heaing">School Details</h1>
                <div className="instructor-info">
                  <div>
                    <div className="input-field">
                      <label htmlFor="">School District</label>
                      <input
                        type="text"
                        required={true}
                        name="school.district"
                        className="input"
                        placeholder="Type Here"
                        onChange={formikProps.handleChange}
                        value={formikProps.values.school.district}
                      />
                      <ErrorMessage
                        name="school.district"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                    <div className="input-fields">
                      <div className="input-field">
                        <label htmlFor="type">School Type</label>
                        <select
                          name="school.type"
                          required={true}
                          className="input select"
                          onChange={formikProps.handleChange}
                          value={formikProps.values.school.type}>
                          <option value="value-1">Private</option>
                          <option value="value-2">value-2</option>
                          <option value="value-3">value-3</option>
                          <option value="value-4">value-4</option>
                        </select>
                        <ErrorMessage
                          name="school.type"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                      <div className="input-field">
                        <label htmlFor="type">Primary Role</label>
                        <select
                          name="school.role"
                          required={true}
                          className="input select"
                          onChange={formikProps.handleChange}
                          value={formikProps.values.school.role}>
                          <option value="value-1">Admin</option>
                          <option value="value-2">value-2</option>
                          <option value="value-3">value-3</option>
                          <option value="value-4">value-4</option>
                        </select>
                        <ErrorMessage
                          name="school.role"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                    </div>
                    <div className="input-field">
                      <label htmlFor="">Primary Licensure Areas</label>
                      <input
                        type="text"
                        required={true}
                        name="school.licenceAreas"
                        className="input"
                        placeholder="Type Here"
                        onChange={formikProps.handleChange}
                        value={formikProps.values.school.licenceAreas}
                      />
                      <ErrorMessage
                        name="school.licenceAreas"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                    <div className="input-field">
                      <label htmlFor="">Race/ethnicity?</label>
                      <input
                        type="text"
                        required={true}
                        name="school.race"
                        className="input"
                        placeholder="Type Here"
                        onChange={formikProps.handleChange}
                        value={formikProps.values.school.race}
                      />
                      <ErrorMessage
                        name="school.race"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                  </div>
                  <div>
                    <div className="input-fields">
                      <div className="input-field">
                        <label htmlFor="">School Name</label>
                        <input
                          type="text"
                          required={true}
                          name="school.name"
                          className="input"
                          placeholder="Type Here"
                          onChange={formikProps.handleChange}
                          value={formikProps.values.school.name}
                        />
                        <ErrorMessage
                          name="school.name"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                      <div className="input-field">
                        <label htmlFor="type">School Setting</label>
                        <select
                          name="school.setting"
                          required={true}
                          className="input select"
                          onChange={formikProps.handleChange}
                          value={formikProps.values.school.setting}>
                          <option value="value-1">Rural</option>
                          <option value="value-2">value-2</option>
                          <option value="value-3">value-3</option>
                          <option value="value-4">value-4</option>
                        </select>
                        <ErrorMessage
                          name="school.setting"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                    </div>
                    <div className="input-field">
                      <label htmlFor="">
                        On average, how many students do you typically work with?
                      </label>
                      <input
                        type="number"
                        required={true}
                        name="school.students"
                        className="input"
                        placeholder="Type Here"
                        onChange={formikProps.handleChange}
                        value={formikProps.values.school.students}
                      />
                      <ErrorMessage
                        name="school.students"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                    <div className="input-fields">
                      <div className="input-field">
                        <label htmlFor="">Grades You are Teaching</label>
                        <input
                          type="text"
                          required={true}
                          name="school.grads"
                          className="input"
                          placeholder="Type Here"
                          onChange={formikProps.handleChange}
                          value={formikProps.values.school.grads}
                        />
                        <ErrorMessage
                          name="school.grads"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                      <div className="input-field">
                        <label htmlFor="">Prior Programming Experience</label>
                        <input
                          type="text"
                          required={true}
                          name="school.experience"
                          className="input"
                          placeholder="Type Here"
                          onChange={formikProps.handleChange}
                          value={formikProps.values.school.experience}
                        />
                        <ErrorMessage
                          name="school.experience"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                    </div>
                    <div className="input-field m-0">
                      <label htmlFor="type">
                        Do you identify as a person with a disability or other chronic conditions?
                      </label>
                      <select
                        required={true}
                        name="school.condition"
                        className="input select"
                        onChange={formikProps.handleChange}
                        value={formikProps.values.school.condition}>
                        <option value="value-1">No</option>
                        <option value="value-2">value-2</option>
                        <option value="value-3">value-3</option>
                        <option value="value-4">value-4</option>
                      </select>
                      <ErrorMessage
                        name="school.condition"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="form-design">
                <h1>Update Password</h1>
                <div className="instructor-info">
                  <div className="input-field">
                    <label htmlFor="">Old Password</label>
                    <input
                      type="password"
                      name="passwords.oldPassword"
                      className="input"
                      placeholder="Type Here"
                      onChange={formikProps.handleChange}
                      value={formikProps.values.passwords?.oldPassword}
                    />
                    <ErrorMessage
                      name="passwords.oldPassword"
                      render={(error) => <span className="error">{error}</span>}
                    />
                  </div>
                  <div className="input-fields">
                    <div className="input-field">
                      <label htmlFor="">New Password</label>
                      <input
                        type="password"
                        name="passwords.newPassword"
                        className="input"
                        placeholder="Type Here"
                        onChange={formikProps.handleChange}
                        value={formikProps.values.passwords?.newPassword}
                      />
                      <ErrorMessage
                        name="passwords.newPassword"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                    <div className="input-field">
                      <label htmlFor="cnfrmPasswork">Confirm New Password</label>
                      <input
                        type="password"
                        name="passwords.confirmPassword"
                        className="input"
                        placeholder="Type Here"
                        onChange={formikProps.handleChange}
                        value={formikProps.values.passwords?.confirmPassword}
                      />
                      <ErrorMessage
                        name="passwords.confirmPassword"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                  </div>
                </div>
                <button
                  type="submit"
                  ref={userFormSubmitRef}
                  style={{
                    position: 'fixed',
                    left: -100,
                    top: -100,
                  }}>
                  Submit
                </button>
              </div>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
