import React from 'react';
import { Attendees } from '../../../interfaces/workshop.interface';

interface Props {
  data: Attendees;
}

const CurrentWorkshop = (props: Props): JSX.Element => {
  const { data } = props;
  return (
    <div className="attendance">
      <div className="attendance-avatar">
        <img src={data.userId.profile} alt="" />
      </div>
      <div className="attendance-desc">
        <h1>
          {data.userId.firstName} {data.userId.lastName}
        </h1>
        <div className="radio-toolbar">
          <label>
            <input type="radio" name={data.userId.id} value="present" />
            <span className="present">Present</span>
          </label>

          <label>
            <input type="radio" name={data.userId.id} value="absent" />
            <span className="absent">Absent</span>
          </label>
        </div>
      </div>
    </div>
  );
};
export default CurrentWorkshop;
