import React, { ChangeEvent } from 'react';

interface Props {
  name: string;
  label: string;
  text: string;
  onChange: (event: ChangeEvent<HTMLInputElement>) => void;
}

export function UploadFileButton(props: Props): JSX.Element {
  return (
    <div className="upload-file-button">
      <div className="section-label">{props.label}</div>
      <div className="upload-section">
        <label className="browser-file-button">
          Browser File
          <input type="file" onChange={props.onChange} name={props.name} hidden={true} />
        </label>
        <div className="upload-description">{props.text}</div>
      </div>
    </div>
  );
}
