import React, { useEffect } from 'react';
import avatarOne from '../../../assets/icons/avatar-1.svg';
import avatarTwo from '../../../assets/icons/avatar-2.svg';
import avatarThree from '../../../assets/icons/avatar-3.svg';
import CurrentWorkshop from '../../components/CurrentWorkshop';
import backBtn from '../../../assets/icons/Path.svg';
import { useHistory } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../../redux';
import { workshopDetailThunk } from '../WorkshopDetail/thunk';
import { RouteChildrenProps } from 'react-router';

export default function CreateWorkshopView(props: RouteChildrenProps<{ id: string }>): JSX.Element {
  const workshop = useAppSelector((state) => state.workshopDetail);
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(workshopDetailThunk(props.match.params.id));
  }, [dispatch]);
  const history = useHistory();
  const cards = [
    { img: avatarOne, name: 'Gabie Sheber', id: 'attendance-01' },
    { img: avatarTwo, name: 'Loni Bowcher', id: 'attendance-02' },
    { img: avatarThree, name: 'Alicia Stanger', id: 'attendance-03' },
    { img: avatarOne, name: 'Gabie Sheber', id: 'attendance-04' },
    { img: avatarTwo, name: 'Loni Bowcher', id: 'attendance-05' },
    { img: avatarThree, name: 'Alicia Stanger', id: 'attendance-06' },
    { img: avatarOne, name: 'Gabie Sheber', id: 'attendance-07' },
    { img: avatarTwo, name: 'Loni Bowcher', id: 'attendance-08' },
    { img: avatarThree, name: 'Alicia Stanger', id: 'attendance-09' },
    { img: avatarOne, name: 'Gabie Sheber', id: 'attendance-10' },
    { img: avatarTwo, name: 'Loni Bowcher', id: 'attendance-11' },
    { img: avatarThree, name: 'Alicia Stanger', id: 'attendance-12' },
    { img: avatarOne, name: 'Gabie Sheber', id: 'attendance-13' },
    { img: avatarTwo, name: 'Loni Bowcher', id: 'attendance-14' },
    { img: avatarThree, name: 'Alicia Stanger', id: 'attendance-15' },
  ];
  const goback = () => {
    history.goBack();
  };
  return (
    <>
      {!workshop.loading && !!workshop.data && (
        <div className="attendance-section">
          <div className="user-section">
            <div className="user-work">
              <div className="user-work-back" onClick={goback}>
                <img src={backBtn} alt="" />
                <p>Back</p>
              </div>
              <div className="user-work-btns">
                <input className="input" type="date" />
              </div>
            </div>
          </div>
          <div className="attendances content">
            {workshop.data.attendees.map((item) => (
              <CurrentWorkshop data={item} />
            ))}
          </div>
        </div>
      )}
    </>
  );
}
